@import url(https://fonts.googleapis.com/css2?family=Syne:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.link-tree{width:100%}.link-tree-container{display:flex;flex-direction:column;justify-content:center;padding:40px 0}.link-tree-image{content:url(/link-tree/static/media/selfie.c8fdf7b0.png);border-radius:50%;width:200px;height:200px;object-fit:contain;margin:auto}.link-tree button{width:250px;height:35px;background:#99C24D;color:white;outline:none;border:none;border-radius:6px;font-family:'Syne', sans-serif;font-weight:bold}.link-tree p{font-family:'Syne', sans-serif;font-size:18px;color:#F18F01;font-weight:bold}.link-tree .button-group-1,.link-tree .button-group-2{display:flex;flex-direction:column;justify-content:space-between}.link-tree .button-group-1{height:90px}.link-tree .button-group-2{height:150px}.link-tree h3{color:#048BA8;font-family:'Syne', sans-serif;font-size:22px}.link-tree-info{color:white;padding-top:18px}.link-tree-info h4{margin:0;font-size:24px}.link-tree-info p{color:white;margin:0}

.App {
  text-align: center;
}

body {
  background-color: #2E4057;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

